.theme-btn-1{
    padding: 12px 24px;
    border-radius: 4px;
    font-size: $font-size-16px;
    line-height: $line-height-28px;
    font-family: $Font-Family-Orbitron-Bold;
    letter-spacing: $letter-spacing-06;
    background-color: $Primary-Color;
    color: $Black-Color;
    text-transform: capitalize;
    text-decoration: none;
    transition: .4s;
    &:hover{
        background-color: $White-Color;
        color: $Black-Color;
    }
}

.theme-btn-border{
    padding: 12px 24px;
    border-radius: 4px;
    font-size: $font-size-16px;
